import request from "@/api/request";

function getListAction(data) {
    return request({
        method: 'get',
        url: '/address/getListAction',
        data
    })
}

function deleteAction(data) {
    return request({
        method: 'get',
        url: '/address/deleteAction',
        data
    })
}

export {
    getListAction,
    deleteAction
}