<template>
    <div>
        <!-- 头部返回 -->
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <!-- 地址列表详情 -->
        <div class="card">
            <div class="card-item" v-for="item in list" :key="item.id">
                <h3 @click="selectAddress(item.id)">{{item.name}}
                    <span :class="[item.is_default ==1?'act':'']">默认</span>
                </h3>
                <div @click="selectAddress(item.id)" class="info">
                    <p>{{item.mobile}}</p>
                    <p>{{item.address}}</p>
                </div>
                <i @click="goAddsite(item.id)">
                    <van-icon name="records" size="33" />
                </i>
                <b>
                    <van-icon name="close" size="33" @click="deladdress(item.id)" />
                </b>
            </div>
        </div>
        <div class="addAddress">
            <div class="addAddress-info" @click="goAdd">
                +新建地址
            </div>
        </div>
    </div>
</template>

<script>
    // 引入接口
    import {
        getListAction,
        deleteAction
    } from '@/api/my/address/index.js'
    import {
        Toast,
        Dialog,
    } from 'vant';
    export default {
        data() {
            return {
                list: []
            };
        },
        computed: {

        },
        created() {

        },
        mounted() {
            this.init()
        },
        methods: {
            init() {
                // 获取地址列表
                getListAction({
                    openId: localStorage.getItem('openId')
                }).then(res => {
                    this.list = res.data.data
                })
            },
            // 返回
            onClickLeft() {
                history.back();
            },
            // 跳转地址修改和新建页面
            goAddsite(id) {
                this.$router.push(`/cart/order/address?id=${id}`)
            },

            selectAddress(id) {
                this.$store.commit("pp", id);
                history.back()

            },
            // 删除地址
            deladdress(id) {
                Dialog.confirm({
                        title: '标题',
                        message: '弹窗内容',
                    })
                    .then(() => {
                        deleteAction({
                            id: id
                        }).then(res => {
                            if (res.data.data) {
                                Toast.success('成功文案');
                                this.init()
                            }
                        })

                    })
                    .catch(() => {
                        // on cancel
                    });

            },
            // 新建地址
            goAdd() {
                this.$router.push('/cart/order/address')
            }
        },
    };
</script>

<style scoped lang="scss">
    .card {
        .card-item {
            position: relative;
            display: flex;
            background-color: #fff;
            justify-content: space-between;
            padding: 20px 10px;
            border-bottom: 1px solid #ccc;

            h3 {
                width: 20%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                text-align: center;
            }

            .info {
                width: 60%;

                p {
                    margin: 5px 0px;
                }
            }

            i {
                display: flex;
                align-items: center;
                margin-right: 10px;

            }

            span {
                width: 35px;
                height: 20px;
                border: 1px solid #f00;
                text-align: center;
                line-height: 20px;
                color: #f00;
                display: none;
                margin: 0px auto;
                margin-top: 5px;
            }

            b {
                display: flex;
                align-items: center;

                color: #f00;
            }

            .act {
                display: block;

            }
        }
    }

    .addAddress {
        .addAddress-info {
            width: 70%;
            color: #f00;
            border: 1px solid #f00;
            height: 40px;
            line-height: 40px;
            text-align: center;
            margin: 0px auto;
        }

        width: 100%;
        position: fixed;
        bottom: 0;
        padding: 10px 0px;
        background-color: #fff;
    }
</style>